import { useState, useEffect, useRef } from 'react';
import { Link, useRouteMatch, useLocation, useHistory } from 'react-router-dom';
import * as MovieAPI from '../fetch.js';
import Searchbar from '../Components/SeachBar/SearchBar';

export default function MoviesView() {
  const location = useLocation();
  const history = useHistory();
  const [movieSearchArray, setMovieSearchArray] = useState('');
  const [query, setQuery] = useState('');
  const firstRender = useRef(true);
  const { url } = useRouteMatch();
  const searchQuery = new URLSearchParams(location.search).get('query');

  useEffect(() => {
    if (firstRender.current) {
      firstRender.current = false;
      return;
    }

    MovieAPI.fetchMovieByName(query).then(movie =>
      setMovieSearchArray(movie.results),
    );
    history.push({
      ...location,
      search: `query=${query}`,
    });
  }, [query]);

  useEffect(() => {
    if (searchQuery !== null) {
      setQuery(searchQuery);
    }
  }, [searchQuery]);

  return (
    <>
      <Searchbar onSubmit={setQuery} />
      {movieSearchArray && (
        <>
          <ul>
            {movieSearchArray.map(item => (
              <li key={item.id}>
                <Link
                  to={{
                    pathname: `${url}/${item.id}`,
                    state: { from: location },
                  }}
                >
                  {item.title}
                </Link>
              </li>
            ))}
          </ul>
        </>
      )}
      {movieSearchArray.length === 0 && (
        <p>There is no movies with such name. Please try again</p>
      )}
    </>
  );
}
